
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.navigation {
  background-color: rgb(38, 90, 187);
  width: 100vw;
  height: 140px;
  position: relative;
}
.navigation .smoothies-btn {
  color: #fff;
  text-decoration: none;
  font-size: 3.5em;
  position: absolute;
  left: 1em;
  top: 0.5em;
  cursor: pointer;
}
.navigation .add-smoothie-btn {
  position: absolute;
  right: 2em;
  bottom: -.8em;
  color: #fff;
  text-decoration: none;
  background-color: rgb(238, 44, 83);
  width: 80px;
  height: 80px;
  border-radius: 50%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 3em;
  font-weight: bold;
  cursor: pointer;
}

.smoothies {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: center;
  margin-top: 3.5em;
  padding: 0 10em;
}
.smoothie {
  width: 350px;
  height: 300px;
  -webkit-box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.75);
          box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.75);
  padding: 2em;
  position: relative;
  margin-bottom: 3.5em;
}
.smoothie .trash {
  position: absolute;
  top: 0.8em;
  right: 0.8em;
  opacity: 0.5;
  font-size: 1.5em;
  cursor: pointer;
}
.smoothie .smoothie-name {
  text-align: center;
  color: #265abb;
  height: 100px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.smoothie .ingredients {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.smoothie .smoothie-ing {
  color: #333;
  background-color: #ddd;
  border-radius: 50px;
  margin: .2em;
  padding: .5em;
}

.add-smoothie {
  justify-items: center;
  margin-top: 5em;
  padding: 0 10em;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
