      -ms-flex-direction: column;
          flex-direction: column;
}
.add-smoothie .title {
  color: rgb(38, 90, 187);
  font-size: 2.5em;
  text-align: center;
}
.add-smoothie .add-smoothie-form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: 600px;
  margin: 3em auto 0;
}
.add-smoothie-form label {
  color: #777;
  margin-bottom: 1em;
}
.add-smoothie-form input {
  border: none;
  border-bottom: 1px solid #444;
  margin-bottom: 3em;
}
.add-smoothie .added-ingredients {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
.add-smoothie .wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
