}
.add-smoothie .add-smoothie-btn {
  -ms-flex-item-align: center;
      align-self: center;
  background-color: rgb(238, 44, 83);
  color: #fff;
  text-transform: uppercase;
  border: none;
  -webkit-box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.75);
          box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.75);
  border-radius: 5px;
  padding: 1em 2em;
  cursor: pointer;
  letter-spacing: 1px;
}
